import React from 'react'
import { BrowserRouter, Route, Switch } from 'react-router-dom'
import Home from '../containers/Home'
import PartOne from '../containers/PartOne'
import PartTwo from '../containers/PartTwo'
import NotFound from '../containers/NotFound'
import About from '../containers/About'

const App = () => (
    <BrowserRouter>
        <Switch>
            <Route exact path="/portafolio-etica/" component={Home} />
            <Route exact path="/portafolio-etica/uno" component={PartOne} />
            <Route exact path="/portafolio-etica/dos" component={PartTwo} />
            <Route exact path="/portafolio-etica/about" component={About} />
            <Route component={NotFound} />
        </Switch>
    </BrowserRouter>
)

export default App
